@font-face {
  font-family: "nanum";
  src: url("/font/나눔손글씨\ 세계적인\ 한글.ttf") format("truetype");
  font-weight: normal;
}

body {
  background-color: #7ebb54;
  font-family: "nanum", sans-serif;
}

#wrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  width: 1000px;
  height: 600px;
  display: flex;
  justify-content: space-between;

  > div {
    width: 480px;
    height: 100%;
    background-color: #fff;
    padding: 40px 60px;
    box-sizing: border-box;
    h2 {
      font-size: 30px;
      font-weight: 700;
      margin-bottom: 20px;
    }
  }
  > .spring {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    padding: 0;
    background-color: transparent;
    margin: auto;
    width: 130px;
    height: 300px;
    display: flex;
    justify-content: space-between;
    > div {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      div {
        width: 25px;
        height: 25px;
        background-color: #7ebb54;
        border-radius: 100%;
      }
    }
    > .springs {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 100%;
      height: 292px;
      div {
        width: 88%;
        height: 18px;
        margin: 0 auto;
        border-radius: 10px;
        background-color: #58575a;
      }
    }
  }
}

.calendar-container {
  height: 350px;
  margin-bottom: 50px;
  .select-month {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    h3 {
      font-size: 28px;
    }
    button {
      width: 24px;
      height: 24px;
      background-color: transparent;
      border: none;
      text-align: center;
      cursor: pointer;
    }
  }
  .days-container {
    width: 350px;
    margin: auto;
    position: relative;
    .days {
      display: grid;
      width: 350px;
      margin: auto;
      grid-template-columns: repeat(7, 1fr);
      .day {
        position: relative;
        text-align: center;
        height: 50px;
        font-size: 24px;
        line-height: 50px;
        &:nth-child(7n + 1) {
          color: #ed2a61;
        }
        &:nth-child(7n) {
          color: #3c6ffa;
        }
        &.today::after {
          content: "";
          position: absolute;
          width: 50px;
          height: 50px;
          top: 0;
          left: 0;
          background-image: url("/images/today.png");
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center center;
        }
      }
      .disable {
        opacity: 0.3;
      }
    }
    .loading {
      visibility: hidden;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(255, 255, 255, 0.5);
      .loader {
        position: absolute;
        top: 100px;
        left: 0;
        right: 0;
        display: block;
        margin: auto;
        color: #3d6c36;
        font-size: 7px;
        animation-delay: -0.16s;
      }
      .loader,
      .loader:before,
      .loader:after {
        border-radius: 50%;
        width: 2.5em;
        height: 2.5em;
        animation-fill-mode: both;
        animation: bblFadInOut 1.8s infinite ease-in-out;
      }
      .loader:before,
      .loader:after {
        content: "";
        position: absolute;
        top: 0;
      }
      .loader:before {
        left: -3.5em;
        animation-delay: -0.32s;
      }
      .loader:after {
        left: 3.5em;
      }

      @keyframes bblFadInOut {
        0%,
        80%,
        100% {
          box-shadow: 0 2.5em 0 -1.3em;
        }
        40% {
          box-shadow: 0 2.5em 0 0;
        }
      }
    }
  }
}
.quotes-container {
  p {
    font-size: 24px;
  }
}
